import React, { useContext } from 'react'
import Locale from '../../components/Locale'
import { LocaleContext } from '../../contexts/LocaleContext'

export default function Instruction (props) {
  const languageType = useContext(LocaleContext)
  return (
    <div onClick={props.onClick} style={{
      display: props.firstVisited === 'Y' ? 'flex' : 'none',
      position: 'fixed',
      left: 0,
      right: 0,
      bottom: 0,
      top: 0,
      overflow: 'hidden',
      backgroundColor: '#fff',
      textAlign: 'center',
      alignItems: 'center',
      justifyContent: 'center',
      flexDirection: 'column',
      zIndex: 10
    }}>
      <img width={257.6} height={297.85} style={{ marginLeft: '30%' }} src={require('../../assets/shake_anime.gif')} />
      <div style={{
        marginTop: 20,
        width: '75%',
        color: '#1b1b1b',
        fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
        fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
        fontSize: languageType.current === 'ja' ? '17px' : '20px'
      }}>
        <Locale code='mix.instruction' />
      </div>
    </div>
  )
}
