import React, { useState, useEffect, useContext } from 'react'
import Popup from '../../components/Popup'
import Locale from '../../components/Locale'
import { LocaleContext } from '../../contexts/LocaleContext'
import { uniqlo } from '../../metalib'
import { Flex } from 'antd-mobile'
import { MakeContext } from '../../contexts/MakeContext';

function StyleButton (props) {
  const { type: filterType, onClick, active, border, ...extraProps } = props
  const languageType = useContext(LocaleContext)

  useEffect(() => {}, [])

  return (
    <div
      onClick={() => {
        onClick()
      }}
      style={{
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        padding: 16,
        borderBottom: border ? 'none' : '1px solid #dadada',
        // boxShadow: 'inset 0 -1px 0 0 #DADADA',
        // height: 56,
        // lineHeight: '56px',
        backgroundColor: '#FFF'
      }}
      {...extraProps}
    >
      <img src={require(`../../assets/${props.icon}`)} height={24} width={24} />
      <div style={{
        fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
        color: '#1b1b1b',
        textAlign: 'center',
        width: '80%',
        fontSize: languageType.current === 'ja' ? '17px' : '20px',
        fontWeight: languageType.current === 'ja' ? 'initial' : 'bold'
      }}>
        <Locale code={props.label} />
      </div>
      {active ? <img src={require(`../../assets/check.svg`)} height={24} width={24} /> : <div style={{ width: 24, height: 24 }} />}
    </div>
  )
}

export default function ModePicker (props) {
  const { selectedMode, setSelectedMode } = props
  const languageType = useContext(LocaleContext)
  const make = useContext(MakeContext)
  const [styleIcon, setStyleIcon] = useState('blend-normal')
  const [popupVisible, setPopupVisible] = useState(false)
  const [modePickerStatus, setModePickerStatus] = useState(false)

  useEffect(() => {
    let layers = make.layers
    let blendfunc = true
    let blendfuncNGnum = 0
    layers.forEach(element => {
      if (element.stampRule) {
        if (element.stampRule.stampFlag.flag_can_other_layer_over === 'NG') {
          blendfuncNGnum = blendfuncNGnum + 1
        }
      }
    })
    if(layers[layers.length - 1].stampRule){
      if(layers[layers.length - 1].stampRule.stampFlag.flag_effect_blendfunc === 'NG'){
          blendfunc = false
      }
      else{
        blendfunc = true
      }
      
    }
    if ((layers.length - blendfuncNGnum) > 1&&blendfunc) {
      setModePickerStatus(true)
    } else {
      setModePickerStatus(false)
    }
  }, [])
  // function createClickHandler (type, closePopup) {
  //   return function clickHandler () {
  //     // props.onChange(type)
  //     setSelectedMode(type)
  //     closePopup()
  //   }
  // }

  function createClickHandler (type, closePopup) {
    return function clickHandler () {
      // props.onChange(type)
      if (type === 'ERASE') {
        setStyleIcon('blend-mask')
      } else if (type === 'ALPHA') {
        setStyleIcon('blend-alpha')
      } else if (type === 'SCREEN') {
        setStyleIcon('blend-screen')
      } else if (type === 'MULTIPLY') {
        setStyleIcon('blend-multiply')
      } else {
        setStyleIcon('blend-normal')
      }
      setSelectedMode(type)
      closePopup()
    }
  }

  function onMouseDownChange () {
    setStyleIcon(styleIcon + '-pressed')
  }

  function onMouseUpChange () {
    let newStyleIcon = styleIcon.replace('-pressed', '')
    setStyleIcon(newStyleIcon)
  }

  return (
    <Popup
      disabled={props.disabled}
      renderOverlay={(overlayProps) => {
        const { triggerRect, closePopup, visible } = overlayProps
        setPopupVisible(visible)
        if (!triggerRect) return <div />
        return (
          <div style={{ position: 'absolute', width: window.innerWidth, bottom: 102, zIndex: 12 }}>
            <div className='txtBold' style={{
              color: '#1B1B1B',
              padding: 16,
              fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
              fontSize: languageType.current === 'ja' ? '17px' : '20px',
              borderBottom: '1px solid #dadada',
              backgroundColor: '#FFF',
              textAlign: 'center',
              fontWeight: languageType.current === 'ja' ? 'initial' : 'bold'
            }}>
              <Locale code='mix.mode.picker.title' />
            </div>
            <div>
              <StyleButton
                active={selectedMode === uniqlo.BlendMode.NORMAL}
                onClick={createClickHandler(uniqlo.BlendMode.NORMAL, closePopup)}
                type='normal' icon='blend-normal.svg' label='mix.mode.picker.normal' />
              <StyleButton
                active={selectedMode === uniqlo.BlendMode.ALPHA}
                onClick={createClickHandler(uniqlo.BlendMode.ALPHA, closePopup)}
                type='alpha' icon='blend-alpha.svg' label='mix.mode.picker.alpha' />
              <StyleButton
                active={selectedMode === uniqlo.BlendMode.SCREEN}
                onClick={createClickHandler(uniqlo.BlendMode.SCREEN, closePopup)}
                type='screen' icon='blend-screen.svg' label='mix.mode.picker.screen' />
              <StyleButton
                active={selectedMode === uniqlo.BlendMode.MULTIPLY}
                onClick={createClickHandler(uniqlo.BlendMode.MULTIPLY, closePopup)}
                type='multiply' icon='blend-multiply.svg' label='mix.mode.picker.multiply' />
              <StyleButton
                border='none'
                active={selectedMode === uniqlo.BlendMode.ERASE}
                onClick={createClickHandler(uniqlo.BlendMode.ERASE, closePopup)}
                type='crop' icon='blend-mask.svg' label='mix.mode.picker.crop' />
            </div>
            <div style={{
              position: 'absolute',
              marginLeft: 26 }}>
              <svg width='30' height='24' viewBox='0 0 30 24'>
                <path
                  fill='#fff'
                  d='M30 0 L15 20 L 0 0 z'
                />
              </svg>
            </div>
          </div>
        )
      }}
    >
     {
       modePickerStatus ? 
        <div
          onTouchStart={() => {
            onMouseDownChange()
          }}
          onTouchEnd={() => {
            onMouseUpChange()
          }}
          style={{
            borderRadius: 50,
            width: 50,
            boxSizing: 'border-box',
            height: 50,
            backgroundColor: 'white',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            border: '1px solid rgb(221, 221, 221)',
            zIndex: popupVisible ? 13 : 11
          }}
        >
          <span>
            <img
              src={require(`../../assets/${styleIcon}.svg`)}
              alt=''
              style={{
                width: '100%',
                height: '100%'
              }}
            />
          </span>
        </div> : <div style={{
            width: 50,
            boxSizing: 'border-box',
            height: 50,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }} />
    }
    </Popup>
  )
}
