export default function getEventPoint (t) {
  const { touches, target } = t
  const { left, top } = target.getBoundingClientRect()
  if (touches) {
    const touch = touches[0]
    if (touch) {
      return { x: touch.clientX - left, y: touch.clientY - top }
    }
  }
  return { x: t.clientX - left, y: t.clientY - top }
}
